<template>
    <div class="order_wrapbox">
        <!-- <div style="display:none;">{{1521557219530 | formatDuring2}}</div> -->
        <div style="display:none;">{{1558629072 | formatDuring2}}</div>
        <!--提交区域 开始-->
        <div class="submit_box">
            <div class="submit_bar">
                <input type="text" class="jd_text" placeholder="请输入或粘贴有效的京东订单号" v-model.trim="orderTextVal">
                <div class="jd_btn" @click="checkOrder">提交</div>
            </div>
            <div class="submit_txt">提示:订单未出现在列表内 可在此处找回</div>
            
            <!--订单数量 开始-->
            <div class="order_allbox">
                <div class="order_bar">
                    <div class="order_num">
                        <p class="order_num_p1">订单总数</p>
                        <p class="order_num_p2">{{list.can}}单</p>
                        <div class="order_num_line"></div>
                    </div>
                    <div class="order_num">
                        <p class="order_num_p1">有效订单</p>
                        <p class="order_num_p2">{{list.can_use}}单</p>
                        <div class="order_num_line"></div>
                    </div>
                    <div class="order_num">
                        <p class="order_num_p1">返款总额</p>
                        <p class="order_num_p2">￥{{list.money}}</p>
                        <div class="order_num_line"></div>
                    </div>
                    <div class="order_num">
                        <p class="order_num_p1">已返款</p>
                        <p class="order_num_p2">￥{{list.get_money}}</p>
                    </div>
                    <div class="order_tip">(次月25号到账)</div>
                </div>
            </div>
            <!--订单数量 结束-->
        </div>
        <!--提交区域 结束-->

        <!--订单列表 开始-->
        <!-- <div class="order_listbox" v-for="item in list.orders" :key="item.orderid"> -->
        <div class="order_listbox" v-for="(item,index) in list.orders" :key="item.index">
            <div class="order_list" >
                <div style="display:none;">{{index}}</div>
                <div class="or_list_title">
                    <div class="or_title_left">{{item.goods_name}}</div>
                    <div class="or_title_right" :class="item.yn == 0?'red_font_color':''">{{item.yn == 0?"无效":"有效"}}订单</div>
                </div>
                <div class="or_list_con">
                    <div class="or_con_p1">单号：{{item.orderid}}</div>
                    <div class="or_con_p1">下单时间：{{item.ordertime | formatDuring2}}</div>
                    <div class="or_con_p1">返款金额： <span>{{item.yn == 0 ? '0' : '98.9'}}个我的币</span></div>
                </div>
            </div>
        </div>
        <!--订单列表 结束-->
  
        <!--空列表 开始-->
        <div class="order_none_record" v-if="!(list.orders !== undefined && list.orders.length >0)">
            <img src="../../../assets/images/none_record.png" class="hm_w_100">
        </div>
        <!--空列表 结束-->

        <!--返回顶部 开始-->
        <return-top></return-top>
        <!--返回顶部 结束-->

        <!--弹窗 开始-->
        <ac-pop :orderPopflag="orderPopflag" @return="returnLook" :orderTitle="orderTitle" :orderContent="orderContent"></ac-pop>
        <!--弹窗 结束-->
    </div>
</template>

<script>
    import axios from 'axios'
    import ReturnTop from 'common/top/ReturnTop'
    import AcPop from 'common/pop/AcPop'
    export default {
        name:'OrderList',
        components:{
            ReturnTop,
            AcPop
        },
        props: {
            list: Object
        },
        data(){
            return {
                checkOrderList:{},
                orderPopflag:false,//订单弹窗标识(true为显示，false为隐藏)
                orderTitle:'默认标题',//订单弹窗标题
                orderContent:'默认内容',//订单弹窗内容
                orderTextVal:''//文本框的值
            }
        },
        filters:{
            //add0(m){return m<10?'0'+m:m },
            formatDuring2(seconds){
                var date = new Date(seconds*1000)
                var year = date.getFullYear();
                var month = date.getMonth() + 1;
                var day = date.getDate() < 10 ? "0" + date.getDate() : date.getDate();
                var hour = date.getHours() < 10 ? "0" + date.getHours() : date.getHours();
                var minute = date.getMinutes() < 10 ? "0" + date.getMinutes() : date.getMinutes();
                var second = date.getSeconds() < 10 ? "0" + date.getSeconds() : date.getSeconds();
                var currentTime = year + "-" + month + "-" + day + "  " + hour + ":" + minute + ":" + second;
                return currentTime
            },
			// formatDuring2(timestamp) {
			// 	var time = new Date(timestamp);
            //     var year = time.getFullYear();
            //     var month = time.getMonth()+1;
            //     var date = time.getDate();
            //     var hours = time.getHours();
            //     var minutes = time.getMinutes();
            //     var seconds = time.getSeconds();

            //     month = month > 9 ? month : '0' + month
			// 	date = date > 9 ? date : '0' + date
			// 	hours = hours > 9 ? hours : '0' + hours
			// 	minutes = minutes > 9 ? minutes : '0' + minutes
			// 	seconds = seconds > 9 ? seconds : '0' + seconds
            //     return year+'-'+month+'-'+date+' '+hours+':'+minutes+':'+seconds;
			// },
		},
        methods:{
            //商品列表
            checkOrder(){
                if(this.orderTextVal == ''){
                    console.log('文本框不能为空');
                    this.orderTitle = "温馨提示"
                    this.orderContent = "请输入或粘贴有效的京东订单号"
                    this.orderPopflag = true
                    return false
                }
                // axios.post('/api/new_jd_get_check_orders',{data:"{'app_id':'1569840'}"}).then(this.getCheckOrderList)
                axios.post('/api/new_jd_get_check_orders',{"data":JSON.stringify({"app_id":localStorage.getItem('app_id'),"orderid":this.orderTextVal?this.orderTextVal:""})}).then(this.getCheckOrderList) 
                // axios.post('/api/new_jd_get_check_orders',{"data":JSON.stringify({"app_id":"1569840","orderid":this.orderTextVal})}).then(this.getCheckOrderList)
            },
            getCheckOrderList(res){
                console.log(res.data)
                res = res.data
                
                if (res.code == 200 && res.data) {
                    // const data = res.data
                    // this.checkOrderList = data
                    
                    this.orderTitle = res.msg
                    this.orderContent = res.data
                    this.orderPopflag = true
                }
            },
            returnLook(){
                this.orderPopflag = false
            }
        }
    }
</script>

<style scope>
/* 订单列表 */
.order_wrapbox:after,.order_listbox:after{content:" ";display:block;height:0;clear:both;visibility:hidden}.order_wrapbox,.order_listbox{zoom:1}
.order_wrapbox,.submit_box,.order_listbox{box-sizing:border-box;-moz-box-sizing:border-box;-webkit-box-sizing:border-box;}
.order_wrapbox{width: 100%;}
/* 提交区域 */
.submit_box{background:#f2f2f2;padding: 0.4rem 0.5rem;height: 1.7rem;margin-bottom: 1rem;position: relative;}
.submit_bar{width: 3.2rem;height: 0.42rem;}
.jd_text{float: left;width: 2.5rem;height:0.42rem;border-radius:4px;background: #fff;text-indent: 0.14rem;border-top-right-radius: 0;border-bottom-right-radius: 0;font-size: 0.1324rem;}
.jd_btn{text-align:center;float: left;width: 0.68rem;height: 0.42rem;line-height: 0.42rem;background: #f10000;font-size: 0.1986rem;color: #fff;border-top-right-radius: 4px;border-bottom-right-radius: 4px;}
.hui_jd_btn{color: #999;}
.submit_txt{text-align:left;font-size: 0.1103rem;color: #999;height: 0.3rem;line-height: 0.3rem;padding:0  0.14rem;}
/* 订单数量 */
.order_allbox{width: 3.78rem;height: 1.12rem;position: absolute;left: 0.18rem;bottom:-0.68rem;background: #f10000;border-radius:14px;box-shadow: 0 5px 10px rgba(0,0,0,0.3);}
.order_bar{position: relative;width: 100%;height: 0.88rem;padding-top:0.24rem;}
.order_num{width: 0.945rem;height: 0.56rem;float: left;position: relative;}
.order_num_p1{padding: 0 0 0.18rem;color: #fff;font-size: 0.1434rem;height: 0.16rem;line-height: 0.16rem;width: 100%;text-align: center;}
.order_num_p2{color: #fff;font-size: 0.2207rem;height: 0.24rem;line-height: 0.24rem;width: 100%;text-align: center;}
.order_num_line{width: 1px;height: 0.56rem;position: absolute;right: 0;top: 0;background-image: url('../../../assets/images/line.png');background-size: 100%;}
.order_tip{position: absolute;font-size: 0.0993rem;line-height:0.12rem;right: 0.16rem;bottom: 0.1rem;color: #fff;}
/* 订单列表 */
.order_listbox{width: 3.78rem;margin: 0 auto;}
.order_list{margin-bottom:0.16rem;border-radius:4px;width: 100%;height: 1.4rem;box-shadow: 0 3px 6px rgba(0,0,0,0.2);background: #f2f2f2;}
.or_list_title{width: 100%;height: 0.44rem;line-height:0.44rem;border-bottom: 1px solid #fff;}
.or_title_left{color:#262626;font-size:0.1655rem;float:left;width: 2.26rem;height: 0.44rem;line-height: 0.44rem;padding-left: 0.18rem;overflow: hidden;white-space: nowrap;text-overflow: ellipsis;}
.or_title_right{float: right;height: 0.44rem;line-height:0.44rem;padding-right: 0.18rem;color: #00aaff;font-size: 0.1655rem;}
.red_font_color{color: #f10000;}
.or_con_p1{padding-left:0.18rem;font-size: 0.1545rem;color: #999;margin: 0.08rem 0 0.06rem;}
.or_con_p1 span{color: #f10000;}
/*无记录*/
.order_none_record{width: 1.28rem;height: 1.32rem;margin: 1.7rem auto 0;}
</style>


