/*rem单位的专用js,所有的psd图请缩放到414大小来切,1rem=100px;因此1px=0.01rem,例如需要设置宽度为15px，则 15*0.01=0.15rem*/
(function (doc, win) {var docEl = doc.documentElement;var resizeEvt = 'orientationchange' in window ? 'orientationchange' : 'resize';var recalc = function () {var clientWidth = docEl.clientWidth;if (!clientWidth) return;if(clientWidth>=640){clientWidth=640;}docEl.style.fontSize = (clientWidth / 414 * 100).toFixed(1) + 'px';};recalc();if (!doc.addEventListener) return;win.addEventListener(resizeEvt, recalc, false);})(document, window);


