<template>
    <div class="home_time">
		
        <!-- <div class="time_start">活动开启倒计时 08天：08时：08分：08秒</div> -->
        <!-- <div class="time_start" v-if="timeFlag">{{ `活动开启倒计时 ${day}天：${hr}时：${min}分：${sec}秒` }}</div> -->
        <div class="time_start" v-if="timeFlag">活动开启倒计时 {{this.systemTime | formatDuring}}</div>
        <div class="time_start" v-if="timeEndFlag">活动结束</div>
    </div>
</template>

<script>
	import axios from 'axios'
    export default {
		name:'HomeTime',
		
        data(){
        	return {
        		day: 0,//天数
        		hr: 0,//小时	
        		min: 0,//分钟
        		sec: 0,//秒数
        		timeFlag:true,//活动开始时间标识
				timeEndFlag:false,//活动结束时间标识
				systemTime:1,//服务器时间
        	}
        },
        mounted(){
			this.countdown()
			// console.log(this.list.time)
			this.time()
		},
		filters:{
			formatDuring(num) {
				var days = Math.floor((num)/(24*3600));
				var hours = Math.floor((num)%(24*3600)/3600);
				var minutes = Math.floor((num)%3600/60);
				var seconds = Math.round((num)%60);
				days = days > 9 ? days : '0' + days
				hours = hours > 9 ? hours : '0' + hours
				minutes = minutes > 9 ? minutes : '0' + minutes
				seconds = seconds > 9 ? seconds : '0' + seconds
				return days + "天：" + hours + "时：" + minutes + "分：" + seconds + "秒";
			},
		},
        methods:{
			//时间
            time(){
				
				axios.post('/api/new_jd_get_type').then(this.getTime)
				
                
            },
            getTime(res){
                // console.log(res.data)
                res = res.data
                if (res.code == 200 && res.data) {
					const data = res.data
					this.systemTime = data.time
                    // this.systemTime = this.getDuration(data.time)
                    
                    //console.log(this.systemTime)
                }
			},

        	countdown(){
				  this.systemTime --

				  if( this.systemTime >=0 ){
					  console.log("活动倒计时中")
					  
				  }else if(this.systemTime < 0 && this.systemTime >= -86400){
					  this.$store.state.acStartTime = false
					  this.$store.state.acEndTime = false
					  this.timeFlag = false
			          console.log("活动正在进行中")
				  }else{
					  this.$store.state.acStartTime = false
					  this.$store.state.acEndTime = true
					  this.timeEndFlag = true
					  this.timeFlag = false
					  console.log("活动结束")
				  }
				//   this.getSystemTime = this.formatDuring(this.systemTime)
				//   this.getSystemTime = this.systemTime
				//   console.log(this.getSystemTime)

				  

			    //   const end = Date.parse(new Date('2019-05-24 00:00:00'))//活动开始时间
			      
			    //   const now = Date.parse(new Date())
				//   const msec = end - now

			    //   if( msec >= 0 ){
				//       let day = parseInt(msec / 1000 / 60 / 60 / 24)
				//       let hr = parseInt(msec / 1000 / 60 / 60 % 24)
				//       let min = parseInt(msec / 1000 / 60 % 60)
				//       let sec = parseInt(msec / 1000 % 60)
				//       this.day = day > 9 ? day : '0' + day
				//       this.hr = hr > 9 ? hr : '0' + hr
				//       this.min = min > 9 ? min : '0' + min
				// 	  this.sec = sec > 9 ? sec : '0' + sec
				//       console.log("活动倒计时中") 
			    //   }else{
			    //       this.timeFlag = false
			    //       console.log("活动正在进行中")
			    //   }

			    //   const end2 = Date.parse(new Date('2019-05-25 00:00:00'))//活动结束时间
			    //   const msec2 = end2 - now
			    //   if( msec2 < 0 ){
			    //   	  console.log("活动已结束")
				// 		this.timeEndFlag = true
				// 		this.$store.state.acEndTime = true
			    //   }



			      const that = this
			      setTimeout(function () {
			        	that.countdown()
			      }, 1000)
		    }
        }
    }
</script>

<style scope>
/* 倒计时 */
.home_time{background:#f2f2f2;width: 100%;}
.time_start{height: 0.52rem;line-height: 0.52rem;text-align: center;font-size: 0.1655rem;color: #262626;}
</style>


