<template>
	<!--活动弹窗 开始-->
	<div class="acpop_block">
		<!--订单已存在 开始-->
		<div class="acpop_wrap" id="orderAlreadyPop" v-if="orderPopflag">
	    	<div class="ac_pop_bg"></div>
			<div class="ac_pop_con">
				<div class="ac_pop_title">
					<span class="ac_pop_title_txt">{{orderTitle}}</span>
				</div>
				<div class="ac_pop_container">
					<div class="ac_content">
						<p>{{orderContent}}</p>
					</div>
					<div class="ac_pop_singlebtn">
						<a href="javascript:void(0);" class="ac_pop_singlelink" @click="orderReturn">返回查看</a>
					</div>
				</div>
			</div>
		</div>
		<!--订单已存在 结束-->

		<!--活动未开启 开始-->
		<div class="acpop_wrap" id="noStartPop" v-if="acStartFlag">
	    	<div class="ac_pop_bg"></div>
			<div class="ac_pop_con">
				<div class="ac_pop_title">
					<span class="ac_pop_title_txt">活动未开启</span>
				</div>
				<div class="ac_pop_container">
					<div class="ac_content">
						<p>活动开启时间为5月24号</p>
						<p>尽请期待</p>
					</div>
					<div class="ac_pop_dbbtn">
						<a href="javascript:void(0);" class="ac_pop_db_left" @click="closeAcPop">关闭</a>
						<router-link to="/rule" class="ac_pop_db_right">查看规则</router-link>
					</div>
				</div>
			</div>
	    </div>
	    <!--活动未开启 结束-->

			<!--活动结束 开始-->
			<div class="acpop_wrap" id="acEndPop" v-if="acEndFlag">
					<div class="ac_pop_bg"></div>
				<div class="ac_pop_con">
					<div class="ac_pop_title">
						<span class="ac_pop_title_txt">活动已结束</span>
						<i class="ac_pop_title_close" @click="closeAcEndPop"></i>
					</div>
					<div class="ac_pop_container ac_container_pb0 ac_container_pt16">
						<div class="ac_content">
							<p>抱歉！</p>
							<p>该活动已结束！</p>
						</div>
					</div>
				</div>
				</div>
				<!--活动结束 结束-->

		<!--温馨提示 开始-->
		<div class="acpop_wrap" id="tipPop"  v-if="onlyOnceFlag">
	    	<div class="ac_pop_bg"></div>
			<div class="ac_pop_con">
				<div class="ac_pop_title">
					<span class="ac_pop_title_txt">温馨提示</span>
				</div>
				<div class="ac_pop_container">
					<div class="ac_tip_con">
						<p>1、活动时间为5月24号当天</p>
						<p>2、每个用户限购8单</p>
						<p>3、支付时必须使用微信支付</p>
						<p>4、...</p>
					</div>
					<div class="ac_pop_dbbtn">
						<a href="javascript:void(0);" class="ac_pop_db_left" @click="closeOnlyOncePop">关闭</a>
						<router-link to="/rule" class="ac_pop_db_right">查看规则</router-link>
					</div>
				</div>
			</div>
	    </div>
	    <!--温馨提示 结束-->

		<!--未找到该订单 开始
		<div class="acpop_wrap" id="noFindPop" style="display:none;">
	    	<div class="ac_pop_bg"></div>
			<div class="ac_pop_con">
				<div class="ac_pop_title">
					<span class="ac_pop_title_txt">未找到该订单</span>
				</div>
				<div class="ac_pop_container">
					<div class="ac_content">
						<p>未找到该订单</p>
						<p>请仔细核对京东订单号</p>
					</div>
					<div class="ac_pop_singlebtn">
						<a href="javascript:void(0);" class="ac_pop_singlelink">查看规则</a>
					</div>
				</div>
			</div>
	    </div>
	    未找到该订单 结束-->



	  <!--订单导入成功 开始
		<div class="acpop_wrap" id="orderSuccessPop" style="display:none;">
	    	<div class="ac_pop_bg"></div>
			<div class="ac_pop_con">
				<div class="ac_pop_title">
					<span class="ac_pop_title_txt">订单导入成功</span>
				</div>
				<div class="ac_pop_container">
					<div class="ac_pic">
						<img src="../../assets/images/success_icon.png" class="hm_w_100">
					</div>
					<div class="ac_pop_singlebtn">
						<a href="javascript:void(0);" class="ac_pop_singlelink">好的</a>
					</div>
				</div>
			</div>
	    </div>
	    订单导入成功 结束-->


	    
	</div>
    <!--活动弹窗 结束-->
</template>

<script>
    export default {
				name:'AcPop',
				props:["orderPopflag","orderTitle","orderContent","acStartFlag","acEndFlag","onlyOnceFlag"],
				methods:{
						//返回查看
						orderReturn(){
							 this.$emit("return")
						},
						//关闭
						closeAcPop(){
							 this.$emit("close")
						},
						//活动关闭
						closeAcEndPop(){
							 this.$emit("closeend")
						},
						//一次性弹窗关闭
						closeOnlyOncePop(){
							 this.$emit("closeonly")
						}
				}
    }
</script>

<style scope>
@import url('../../assets/styles/acpop.css');
</style>


