<template>
  <div id="app">
  	<!-- <keep-alive>
      <router-view/>
    </keep-alive> -->

    <!--返回按钮 开始-->
    <div class="return_btn" @click="goBack" v-show="flag"></div>
    <!--返回按钮 结束-->
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'App',
  data(){
    return {
      flag:false
    }
  },
  created(){
    this.flag = this.$route.path === "/home" ? false : true
  },
  methods:{
    goBack(){
      //点击后退
      this.$router.go(-1)
    }
  },
  watch:{
    "$route.path":function(newVal){
        if(newVal == "/home"){
          this.flag = false
        }else{
          this.flag = true
        }
    }
  }
}
</script>

<style>
*{-webkit-overflow-scrolling: touch;}
.return_btn{width: 0.34rem;height: 0.34rem;background-image:url('assets/images/return_btn.png');background-size:100%;position: fixed;top: 0.25rem;left: 0.25rem;z-index:99;}
</style>



