<template>

    <div class="shop_wrapbox">
        <div id="pullContain" class="rec_wraplist">
            <pull-to @infinite-scroll="getMoreList" class="file-lists" style="height:7.2rem;">
                <ul>
                    <li v-for="(item,index) in dataList" :key="index" class="rec_bar" @click="goUrl(item.goods_id)">
                        <div class="rec_pic">
                            <img :src="item.img" class="hm_w_100">
                        </div>
                        <div class="rec_con">
                            <div class="rec_title">{{item.title}}</div>
                            <div class="rec_sale">销量{{item.sell_number}}</div>
                            <div class="rec_price">京东价 ￥{{item.price_before}} </div>
                            <div class="rec_final_price">券后价 <span>￥{{item.price_after}}</span></div>
                            <div class="rec_discount">返 ￥{{item.easy_price}}</div>
                        </div>
                    </li>
                </ul>
            </pull-to>
        </div>


        <!-- <div id="pullContain">
            <pull-to @infinite-scroll="getMoreList" class="file-lists" style="height:7.2rem;">
                <div class="rec_wraplist" v-for="item in dataList" :key="item.img">
                    <div class="rec_bar" @click="goUrl(item.goods_id)">
                        <div class="rec_pic">
                            <img :src="item.img" class="hm_w_100">
                        </div>
                        <div class="rec_con">
                            <div class="rec_title">{{item.title}}</div>
                            <div class="rec_sale">销量{{item.sell_number}}</div>
                            <div class="rec_price">京东价 ￥{{item.price_before}} </div>
                            <div class="rec_final_price">券后价 <span>￥{{item.price_after}}</span></div>
                            <div class="rec_discount">返 ￥{{item.easy_price}}</div>
                        </div>
                    </div>
                </div>
            </pull-to>
        </div> -->

        <!--返回顶部 开始-->
        <return-top></return-top>
        <!--返回顶部 结束-->

        <!--弹窗 开始-->
        <ac-pop :acStartFlag="acStartFlag" @close="acClose"></ac-pop>
        <ac-pop :acEndFlag="acEndFlag" @closeend="acEndClose"></ac-pop>
        <!--弹窗 结束-->

        
    </div>
</template>

<script>
    import axios from 'axios'
    import ReturnTop from 'common/top/ReturnTop'
    import AcPop from 'common/pop/AcPop'


    import PullTo from 'vue-pull-to'
    export default {
        name:'OrderList',
        components:{
            ReturnTop,
            AcPop,
            PullTo
        },
        props: {
            list: Array
        },
        data(){
            return {
                acStartFlag:false,//活动弹窗,false为因此，true为显示
                acEndFlag:false,//活动结束弹窗,false为因此，true为显示
                dataList: [],
                pageIndex:1,
                cid:this.$route.params.cid,
                systemTime:1,//服务器时间
            }
        },
        mounted(){
            this.shop()
            console.log("app_id："+localStorage.getItem('app_id'))

            this.countdown()
			// console.log(this.list.time)
			this.time()
        },
        methods:{
            //时间
            time(){
				
				axios.post('/api/new_jd_get_type').then(this.getTime)
				
                
            },
            getTime(res){
                // console.log(res.data)
                res = res.data
                if (res.code == 200 && res.data) {
					const data = res.data
					this.systemTime = data.time
                    // this.systemTime = this.getDuration(data.time)
                    
                    //console.log(this.systemTime)
                }
            },
            countdown(){
				  this.systemTime --

				  if( this.systemTime >=0 ){
					  console.log("活动倒计时中")
					  
				  }else if(this.systemTime < 0 && this.systemTime >= -86400){
					  this.$store.state.acStartTime = false
					  this.$store.state.acEndTime = false
					  this.timeFlag = false
			          console.log("活动正在进行中")
				  }else{
					  this.$store.state.acStartTime = false
					  this.$store.state.acEndTime = true
					  this.timeEndFlag = true
					  this.timeFlag = false
					  console.log("活动结束")
				  }
				//   this.getSystemTime = this.formatDuring(this.systemTime)
				//   this.getSystemTime = this.systemTime
				//   console.log(this.getSystemTime)

				  

			    //   const end = Date.parse(new Date('2019-05-24 00:00:00'))//活动开始时间
			      
			    //   const now = Date.parse(new Date())
				//   const msec = end - now

			    //   if( msec >= 0 ){
				//       let day = parseInt(msec / 1000 / 60 / 60 / 24)
				//       let hr = parseInt(msec / 1000 / 60 / 60 % 24)
				//       let min = parseInt(msec / 1000 / 60 % 60)
				//       let sec = parseInt(msec / 1000 % 60)
				//       this.day = day > 9 ? day : '0' + day
				//       this.hr = hr > 9 ? hr : '0' + hr
				//       this.min = min > 9 ? min : '0' + min
				// 	  this.sec = sec > 9 ? sec : '0' + sec
				//       console.log("活动倒计时中") 
			    //   }else{
			    //       this.timeFlag = false
			    //       console.log("活动正在进行中")
			    //   }

			    //   const end2 = Date.parse(new Date('2019-05-25 00:00:00'))//活动结束时间
			    //   const msec2 = end2 - now
			    //   if( msec2 < 0 ){
			    //   	  console.log("活动已结束")
				// 		this.timeEndFlag = true
				// 		this.$store.state.acEndTime = true
			    //   }



			      const that = this
			      setTimeout(function () {
			        	that.countdown()
			      }, 1000)
            },
            
            getMoreList() {
                // console.log('滚动到底部加载...')
                this.pageIndex++
                this.shop()
            },
            //商品列表
            shop(){
                
                axios.post('/api/new_jd_get_list',{"data":JSON.stringify({"pageindex":this.pageIndex,"cid":this.cid})}).then(this.getShopList)  
                // axios.get('/api/new_jd_get_list.json',{"data":JSON.stringify({"pageindex":this.pageIndex,"cid":this.cid})}).then(this.getShopList)  
            },
            getShopList(res){
                // console.log(res.data)
                res = res.data
                if (res.code == 200 && res.data) {
                    const data = res.data
                    this.dataList = this.dataList.concat(data)
                }
            },
            
            //跳转外链
            goUrl(id){
                // axios.post('/api/new_jd_get_change_url',{data:"{'app_id':'1569840','goods_id':'"+id+"'}"}).then(res=>{
                    axios.post('/api/new_jd_get_change_url',{"data":JSON.stringify({"app_id":localStorage.getItem('app_id'),"goods_id":id})}).then(res=>{
                    // axios.post('/api/new_jd_get_change_url',{"data":JSON.stringify({"app_id":"4693063","goods_id":id})}).then(res=>{
                    if(this.$store.state.acStartTime){
                        this.acStartFlag = true
                        return;
                    }else if(this.$store.state.acEndTime){
                        this.acEndFlag = true
                        return;
                    }
                    console.log(res.data.data.data)
                    if(res.data.data.status_code == 200){
                        window.location.href =res.data.data.data
                    }
                }) 
            },

            //关闭按钮
            acClose(){
                this.acStartFlag = false
            },
            //活动结束关闭按钮
            acEndClose(){
                this.acEndFlag = false
            }
        }
    }
</script>

<style scope>
/* 商品容器 */
.shop_wrapbox{padding: 0.18rem 0.18rem 0; touch-action: none;}
/* 列表 */
.rec_wraplist{width: 100%;}
.rec_bar{width: 100%;height: 1.49rem;background: #f2f2f2;margin-bottom: 0.16rem;position: relative;}
.rec_pic{float: left;width: 1.25rem;height: 1.25rem;margin: 0.12rem;}
.rec_pic img{display: block;width: 100%;height: 100%;}
.rec_con{float: left;width: 2.2rem;height: 1.24rem;position: absolute;right: 0.1rem;}
.rec_title{text-align: left;color: #262626;font-size: 0.1655rem;height: 0.18rem;line-height: 0.18rem;margin: 0.1rem 0 0.2rem;overflow: hidden;white-space: nowrap;text-overflow: ellipsis;}
.rec_sale{text-align: left;font-size: 0.1103rem;color: #999;height: 0.14rem;line-height: 0.14rem;margin-bottom: 0.14rem;}
.rec_price{text-align: left;font-size: 0.1324rem;color: #999;height: 0.14rem;line-height: 0.14rem;margin-bottom: 0.08rem;}
.rec_final_price{text-align: left;font-size: 0.1103rem;color: #999;}
.rec_final_price span{font-size: 0.1986rem;color: #f10000;}
.rec_discount{background:url(../../../assets/images/card_bg.png) no-repeat;background-size:100%;position: absolute;top:0.65rem;right:0;width: 0.99rem;height:0.33rem;line-height: 0.33rem;text-align: center;font-size: 0.1324rem;color: #fff;}
</style>


