<template>
    <!-- <div class="home_category">
    	<div class="category_box" v-for="item in homeCategory">
    		<div class="cateory_span">{{item.cid}}</div>
    		<div class="cateory_span">{{item.cname}}</div>
    		<div class="cateory_span">{{item.img_url}}</div>
    	</div>
    </div> -->
	  <div class="icons">
	    <swiper :options="swiperOption">
	      <swiper-slide v-for="(page, index) of pages" :key="index">
	        <router-link :to="'/shop/'+item.cid"
	          class="icon"
	          v-for="item of page"
	          :key="item.id"
            
	        >
	          <div class='icon-img'>
	            <img class='icon-img-content' :src='item.img_url' />
	          </div>
	          <p class="icon-desc">{{item.cname?item.cname:'热门'}}</p>
	        </router-link>
	      </swiper-slide>
	      <div class="swiper-pagination"  slot="pagination"></div>
	    </swiper>
	  </div>
</template>

<script>
/*    export default {
        name:'HomeCategory',
        props:{
		    homeCategory: Object
		}
    }*/
import axios from 'axios'
export default {
  name: 'HomeCategory',
  props: {
    list: Array
  },
  data () {
    return {
      swiperOption: {
      	pagination: '.swiper-pagination',
        autoplay: false
      }
    }
  },
  computed: {
    pages () {
      const pages = []
      this.list.forEach((item, index) => {
        const page = Math.floor(index / 8)
        if (!pages[page]) {
          pages[page] = []
        }
        pages[page].push(item)
      })
      return pages
    }
  }
}
</script>

<style lang="stylus">


  @import '~styles/varibles.styl'
  @import '~styles/mixins.styl'
  /* 分类 */
  .home_category{width: 100%;background: #fff;height: 1.94rem;}
  .icons{position:relative;z-index:9;border-bottom:0.1rem solid #f2f2f2;}
  .icons >>> .swiper-container
    height: 0
    padding-bottom: 50%
  .icons
    background:#fff
    .icon
      position: relative
      overflow: hidden
      float: left
      width: 25%
      height: 0
      padding-bottom: 22%
      .icon-img
        position: absolute
        top: 0
        left: 0
        right: 0
        bottom: .44rem
        box-sizing: border-box
        padding: .1rem
        .icon-img-content
          display: block
          margin: 0 auto
          height: 0.5rem
          width:0.5rem
      .icon-desc
        position: absolute
        left: 0
        right: 0
        bottom: 0
        height: .34rem
        line-height: .34rem
        text-align: center
        color: $darkTextColor
        ellipsis()
        font-size:0.1325rem
        color:#666
    .swiper-pagination{pointer-events:none;}
    .swiper-container-horizontal>.swiper-pagination-bullets{bottom: -0.2rem;}
.swiper-pagination-fraction,.swiper-pagination-custom,.swiper-container-horizontal > .swiper-pagination-bullets{bottom: -0.2rem;left: 0;width: 100%;pointer-events:none}
.swiper-container-horizontal >.swiper-pagination-bullets .swiper-pagination-bullet {margin: 0 3px;}
.swiper-pagination-bullet{width:0.1rem;height:0.02rem;opacity:0.15;}
.swiper-pagination-bullet-active {opacity: 1;background: #f10000;}
</style>


