<template>
    <div>
        <div>
            <pull-to @infinite-scroll="getMoreList" class="file-lists" style="height:7.2rem;">
                <home-header></home-header>
                <home-time></home-time>
                <home-category :list="iconList"></home-category>
                <!-- <home-recommend :list="shopList"></home-recommend> -->

                

                

                <div class="home_recommend">
                    <div class="recommend_title">精品推荐</div>

                    <div class="rec_wraplist" v-for="item in shopList" :key="item.img">
                        <div class="rec_bar" @click="goUrl(item.goods_id)">
                            <div class="rec_pic">
                                <img :src="item.img" class="hm_w_100">
                            </div>
                            <div class="rec_con">
                                <div class="rec_title">{{item.title}}</div>
                                <div class="rec_sale">销量{{item.sell_number}}</div>
                                <div class="rec_price">京东价 ￥{{item.price_before}} </div>
                                <div class="rec_final_price">券后价 <span>￥{{item.price_after}}</span></div>
                                <div class="rec_discount">返 ￥{{item.easy_price}}</div>
                            </div>
                        </div>
                    </div>

                    
                </div>
            </pull-to>

            <!--弹窗 开始-->
            <ac-pop :acStartFlag="acStartFlag" @close="acClose"></ac-pop>
            <ac-pop :acEndFlag="acEndFlag" @closeend="acEndClose"></ac-pop>
            <!--弹窗 结束-->

            <!--弹窗 开始-->
            <ac-pop :onlyOnceFlag="onlyOnceFlag" @closeonly="closeOnlyOncePop"></ac-pop>
            <!--弹窗 结束-->
            <router-link to="/rule" class="rule_btn"></router-link>
            <router-link to="/list" class="my_roder_btn"></router-link>

            <!--返回顶部 开始-->
            <return-top></return-top>
            <!--返回顶部 结束-->
        </div>
    </div>
</template>

<script>
    import HomeHeader from './components/HomeHeader'
    import HomeTime from './components/HomeTime'
    import HomeCategory from './components/HomeCategory'
    import HomeRecommend from './components/HomeRecommend'
    import ReturnTop from 'common/top/ReturnTop'


    import axios from 'axios'

    import AcPop from 'common/pop/AcPop'

    import PullTo from 'vue-pull-to'
    export default {
        name:'Home',
        components:{
            HomeHeader,
            HomeTime,
            HomeCategory,
            HomeRecommend,
            AcPop,
            PullTo,
            ReturnTop
        },
        data(){
            return {
                
                iconList:[],//分类数据
                shopList:[],//商品列表数据
                app_id:this.$route.query.app_id,//获取app_id,
                onlyOnceFlag:true,
                dataList: [],
                pageIndex:1,
                acStartFlag:false,//活动弹窗,false为因此，true为显示
                acEndFlag:false//活动结束弹窗,false为因此，true为显示
            }
        },
        methods:{
            getMoreList() {
                // console.log('滚动到底部加载...')
                this.pageIndex++
                this.shop()
            },
            //跳转外链
            goUrl(id){
                // axios.post('/api/new_jd_get_change_url',{data:"{'app_id':'1569840','goods_id':'"+id+"'}"}).then(res=>{
                    axios.post('/api/new_jd_get_change_url',{"data":JSON.stringify({"app_id":localStorage.getItem('app_id'),"goods_id":id})}).then(res=>{
                    if(this.$store.state.acStartTime){
                        this.acStartFlag = true
                        return;
                    }else if(this.$store.state.acEndTime){
                        this.acEndFlag = true
                        return;
                    }
                    if(res.data.data.status_code == 200){
                        console.log(1)
                        window.location.href =res.data.data.data
                    }
                }) 
            },
            //关闭按钮
            acClose(){
                this.acStartFlag = false
            },
            //活动结束关闭按钮
            acEndClose(){
                this.acEndFlag = false
            },

            //分类
            category(){
                axios.post('/api/new_jd_get_type').then(this.getCategory)
            },
            getCategory(res){
                // console.log(res.data)
                res = res.data
                if (res.code == 200 && res.data) {
                    const data = res.data
                    this.iconList = data.type
                }
            },

            //商品列表
            shop(){
                // axios.post('/api/new_jd_get_list',{data:"{'pageindex':'1'}"}).then(this.getShopList)
                axios.post('/api/new_jd_get_list',{"data":JSON.stringify({"pageindex":this.pageIndex})}).then(this.getShopList)
                
            },
            getShopList(res){
                // console.log(res.data)
                res = res.data
                if (res.code == 200 && res.data) {
                    const data = res.data
                    this.shopList = this.shopList.concat(data)
                }
            },

            closeOnlyOncePop(){
                this.onlyOnceFlag = false
                localStorage.setItem('onlyOnce','1')
            },

            judgeOnlyOnce(){
                if(localStorage.getItem('onlyOnce') == 1){
                    
                    this.onlyOnceFlag = false

                }
            },

            getAppId(){
                localStorage.setItem('app_id',this.$route.query.app_id)
                console.log("app_id:"+localStorage.getItem('app_id'))
            }

        },
        created(){
            this.getAppId()
        },
        mounted(){
            
            this.category()
            this.shop()
            this.judgeOnlyOnce()
        }
    }
</script>

<style scope>
/* 规则和订单按钮 */
.rule_btn{display:block;width: 0.66rem;height: 0.66rem;background-image: url('../../assets/images/rule_icon.png');background-size:100%;position: fixed;right: 0.21rem;bottom: 2rem;z-index:99;}
.rule_btn{animation:2s 2s infinite hmScale;display:block;}
@keyframes hmScale{0%{transform:scale(1);}10%{transform: scale(0.85);}20%{transform:scale(1);}30%{transform:scale(0.85);}40%{transform:scale(1);}}
.my_roder_btn{width: 1.24rem;height: 0.54rem;background-image: url('../../assets/images/order_icon.png');background-size:100%;position: fixed;right: 0;bottom: 1.4rem;z-index:99;}


/* 精品推荐 */
.home_recommend:after,.rec_wraplist:after,.rec_bar:after{content:" ";display:block;height:0;clear:both;visibility:hidden}.home_recommend,.rec_wraplist,.rec_bar{zoom:1}
.home_recommend,.rec_wraplist,.rec_bar{box-sizing:border-box;-moz-box-sizing:border-box;-webkit-box-sizing:border-box;}
.home_recommend{padding:0.22rem 0.18rem 0;background: #fff;}
.recommend_title{margin-bottom:0.16rem;text-align:left;height:0.22rem;line-height:0.22rem;font-size: 0.1986rem;color: #262626;}
/* 列表 */
.rec_wraplist{width: 100%;}
.rec_bar{width: 100%;height: 1.49rem;background: #f2f2f2;margin-bottom: 0.16rem;position: relative;}
.rec_pic{float: left;width: 1.25rem;height: 1.25rem;margin: 0.12rem;}
.rec_pic img{display: block;width: 100%;height: 100%;}
.rec_con{float: left;width: 2.2rem;height: 1.24rem;position: absolute;right: 0.1rem;}
.rec_title{text-align: left;color: #262626;font-size: 0.1655rem;height: 0.18rem;line-height: 0.18rem;margin: 0.1rem 0 0.2rem;overflow: hidden;white-space: nowrap;text-overflow: ellipsis;}
.rec_sale{text-align: left;font-size: 0.1103rem;color: #999;height: 0.14rem;line-height: 0.14rem;margin-bottom: 0.14rem;}
.rec_price{text-align: left;font-size: 0.1324rem;color: #999;height: 0.14rem;line-height: 0.14rem;margin-bottom: 0.08rem;}
.rec_final_price{text-align: left;font-size: 0.1103rem;color: #999;}
.rec_final_price span{font-size: 0.1986rem;color: #f10000;}
.rec_discount{background:url(../../assets/images/card_bg.png) no-repeat;background-size:100%;position: absolute;top:0.65rem;right:0;width: 0.99rem;height:0.33rem;line-height: 0.33rem;text-align: center;font-size: 0.1324rem;color: #fff;}
</style>


