<template>
    <!--返回顶部 开始-->
    <div id="goTop">
        <div class="goTop" v-show="goTopShow" @click="goTop">
            <i class="el-icon-caret-top goTopIcon"></i>
        </div>
    </div>
    <!--返回顶部 结束-->
</template>

<script>
export default {
    name: "goTop",
    data() {
        return {
            scrollTop: "",
            goTopShow: false
        };
    },
    watch: {
        scrollTop(val) {
            if (this.scrollTop > 500) {
                this.goTopShow = true;
            } else {
                this.goTopShow = false;
            }
        }
    },
    methods: {
        handleScroll() {
            this.scrollTop =
                window.pageYOffset ||
                document.documentElement.scrollTop ||
                document.body.scrollTop;
            if (this.scrollTop > 500) {
                this.goTopShow = true;
            }
        },
        goTop() {
            let timer = null,
                _that = this;
            cancelAnimationFrame(timer);
            timer = requestAnimationFrame(function fn() {
                if (_that.scrollTop > 0) {
                    _that.scrollTop -= 50;
                    document.body.scrollTop = document.documentElement.scrollTop =
                        _that.scrollTop;
                    timer = requestAnimationFrame(fn);
                } else {
                    cancelAnimationFrame(timer);
                    _that.goTopShow = false;
                }
            });
        }
    },
    mounted() {
        window.addEventListener("scroll", this.handleScroll);
    },
    destroyed() {
        window.removeEventListener("scroll", this.handleScroll);
    }
}
</script>

<style scope>
/* 返回顶部 */
.goTop {width: 0.46rem;height: 0.46rem;background-image: url(../../assets/images/top_icon.png);background-size: 100%;position:fixed;right: 0.28rem;bottom: 1.12rem;z-index:100;}
</style>


