<template>
    <div>
        <order-list :list="orderList"></order-list>

    </div>
</template>

<script>
    import axios from 'axios'
    import OrderList from './components/OrderList'
    export default {
        name:'List',
        components:{
            OrderList
        },
        data(){
            return {
                orderList:{},//商品列表数据
            }
        },
        methods:{
            //商品列表
            order(){
                // axios.post('/api/new_jd_get_orders',{data:"{'app_id':'1569840'}"}).then(this.getOrderList)
                axios.post('/api/new_jd_get_orders',{"data":JSON.stringify({"app_id":localStorage.getItem('app_id')})}).then(this.getOrderList)
            },
            getOrderList(res){
                console.log(res.data)
                res = res.data
                if (res.code == 200 && res.data) {
                    const data = res.data
                    this.orderList = data
                }
            }
        },
        mounted(){
            this.order()
            console.log("app_id："+localStorage.getItem('app_id'))
        }
    }
</script>

<style scope>
</style>


