import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/pages/home/Home'
import List from '@/pages/list/List'
import Shop from '@/pages/shop/Shop'
import Rule from '@/pages/rule/Rule'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/home',
      name: 'Home',
      component: Home
    },
    {
      path: '/list',
      name: 'List',
      component: List
    },
    {
      path: '/shop/:cid',
      name: 'Shop',
      component: Shop
    },
    {
      path: '/rule',
      name: 'Rule',
      component: Rule
    }
  ]
})



